const colorList = ["rgb(0, 0, 0)", "rgb(255, 0, 0)", "rgb(233, 30, 99)", "rgb(156, 39, 176)", "rgb(103, 58, 183)", "rgb(63, 81, 181)", "rgb(33, 150, 243)", "rgb(3, 169, 244)", "rgb(0, 188, 212)", "rgb(0, 150, 136)", "rgb(76, 175, 80)", "rgb(139, 195, 74)", "rgb(205, 220, 57)", "rgb(158, 224, 122)", "rgb(255, 235, 59)", "rgb(255, 193, 7)", "rgb(255, 152, 0)", "rgb(255, 205, 210)", "rgb(255, 87, 34)", "rgb(121, 85, 72)", "rgb(158, 158, 158)", "rgb(96, 125, 139)", "rgb(48, 63, 70)", "rgb(255, 255, 255)", "rgb(56, 53, 53)", "rgb(56, 53, 53)", "rgb(56, 53, 53)", "rgb(56, 53, 53)", "rgb(56, 53, 53)", "rgb(56, 53, 53)"]


const initialState = colorList;

export default function reducer(state = initialState, action = {}) {
  switch (action.type) {
    case 'CHANGE_COLOR_ITEM':
      return action.getState(state)
    default:
      return state
  }
  
}

export const changeColor = (index, value) => {
  return {
    type: 'CHANGE_COLOR_ITEM',
    getState(state) {
      return [
        ...state.slice(0, index),
        value,
        ...state.slice(index + 1),
      ]
    }
  }
}